import { graphql } from 'gatsby'
import React, { useState, useCallback } from 'react'
import {
  Stack,
  Navbar,
  Container,
  Footer,
  FloatingScrollButton
} from '../components'
import { GatsbyImage, getImage } from 'gatsby-plugin-image'
import ImageViewer from 'react-simple-image-viewer'
const navbarItems = [{ label: 'back to landing page', link: '/' }]
export default function Projects (props) {
  const [currentImage, setCurrentImage] = useState(0)
  const [isViewerOpen, setIsViewerOpen] = useState(false)
  const openImageViewer = useCallback(index => {
    setCurrentImage(index)
    setIsViewerOpen(true)
  }, [])

  const closeImageViewer = () => {
    setCurrentImage(0)
    setIsViewerOpen(false)
  }
  const getSrcImageData = images => {
    return images.map(item => item.childImageSharp.fluid.srcWebp)
  }
  //extract data

  const gatsbyData = props.data.allJson.nodes[0].data
  const images = props.data.allFile.nodes
  if(!gatsbyData) return null;
  const { title, desc, challanges, stack } = gatsbyData
  return (
    <>
      <Container bgColor='light'>
        <Navbar isSticky={false} navbarItems={navbarItems} />
        <section className='projects-details'>
          <div className='projects-details__main-content'>
            <div>
              <div className='projects-details__main-content__overview'>
                <h1>{title} </h1>
                <p> {desc} </p>
              </div>
              <div className='projects-details__main-content__off-side'>
                <div className='projects-details__main-content__challanges'>
                  <h3>Challanges</h3>
                  <ol>
                    {challanges.map((item, index) => {
                      return (
                        <li
                          key={'challange_item_' + index}
                          className='projects-details__main-content__challanges__item'
                        >
                          {item}
                        </li>
                      )
                    })}
                  </ol>
                </div>
                <div className='projects-details__main-content__tech'>
                  <h3>Tech I used</h3>
                  <ol className='projects-details__main-content__tech__stack'>
                    {stack.map((item, index) => {
                      return (
                        <Stack
                          key={index}
                          {...item}
                          containerStyle={{ marginBottom: '1em' }}
                          id={index}
                        />
                      )
                    })}
                  </ol>
                </div>
              </div>
            </div>
          </div>
          <div className='projects-details__images'>
            {/* images here */}
            {images.map((item, index) => {
              return (
                <GatsbyImage
                  onClick={() => openImageViewer(index)}
                  key={'project_details_image_' + index}
                  image={getImage(item)}
                  alt={"project details image "+index}
                  style={{
                    marginBottom: '1em',
                    marginRight: '1em',
                    cursor: 'pointer'
                  }}
                />
              )
            })}
            {isViewerOpen && (
              <ImageViewer
                src={getSrcImageData(images)}
                currentIndex={currentImage}
                disableScroll={true}
                closeOnClickOutside={true}
                onClose={closeImageViewer}
              />
            )}
          </div>
        </section>
        <FloatingScrollButton />
      </Container>
      <Container bgColor='dark'>
        <Footer navbarItems={navbarItems} />
      </Container>
    </>
  )
}
export const query = graphql`
  query MyQuery($slug: String, $imagesPath: String) {
    allJson(filter: { slug: { eq: $slug } }) {
      nodes {
        id
        data {
          title
          challanges
          desc
          stack {
            title
            src
          }
        }
      }
    }
    allFile(filter: { relativeDirectory: { eq: $imagesPath } }) {
      nodes {
        childImageSharp {
          fluid {
            src
            srcWebp
          }
          gatsbyImageData(layout: CONSTRAINED, placeholder: BLURRED, width: 295)
        }
      }
    }
  }
`
